@import '../../../assets/var';


.employee-form-add {
  table {
    width: 100%;
    display: inline-table;
    height: 100px;
    border:1px solid lightgray;
    border-radius: 5px;
    padding:20px;
    overflow-y: scroll;
    th{
      // text-align: right;
      border-bottom: 1px solid lightgray;
      padding-bottom: 20px;
    }
    th:nth-child(1){
      width: 70%;
      text-align: left;
    }
    tr:nth-child(even){
      height:100px!important;
      background: #efefef;
      padding:0!important;
      
    }
    td{
      padding:0;
      padding-left:20px;
      font-weight: 1.5em;
    }
    svg{
      box-sizing: border-box;
      path{
        fill:green
      }
    }
  }

  .employee-inputs {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    input {
      background: #e0e0e0;
      height: 50px;
      border-radius: 5px;
      border:1px solid gray;
      padding: 20px;
      box-sizing: border-box;
      margin: 10px;
      width: 45%;
    }
    button{
      background: $hellodati-blue;
      height: 50px;
      border-radius: 5px;
      border:none;
      box-sizing: border-box;
      margin: 10px;
      width: 45%; 
      color: $hellodati-yellow;
      font-size:1.2em;
    }
  }
}