.message-erreur {
  color: red;
  margin: 20px;
}

.message-succes {
  color: green;
  margin: 20px;
  width: 50%;
}

.restaurant-form-container {
  display: flex;
  // flex-wrap: wrap;
}

.form-navigator {
  margin-left: -10em;
}

.restaurant-c15-phone {
  width: 100%;
  position: relative;
}

.restaurant-preview-box {
  position: relative;
  top: -430px;
  width: 88%;
  height: 18%;
  background-color: #ffffff;
  margin-left: 14px;
  border-radius: 5px;
  z-index: 1;
  border-bottom: 4.5px solid #739147;
}

.restaurant-preview-box p {
  position: absolute;
  font-size: 12.7px;
  width: 79px;
  margin-left: 7px;
  word-wrap: break-word;
  height: 30px;
  overflow: hidden;
  color: #739147;
}

.restaurant-preview-box img {
  position: absolute;
  width: 58%;
  height: 100%;
  left: 92px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.arrow-right {
  position: relative;
  left: 105px;
  top: 160px;
  border-top: 6.5px solid transparent;
  border-bottom: 6.5px solid transparent;
  border-left: 6.5px solid #ffffff;
  z-index: 2;
}