@import '../../../assets/var';

.employee-form{
  width: 100%;
  // height: 230px;
  background-color: white;
  border-radius: 5px;
}

.employee-form .employee-header{
  width: 100%;
  background-color: $hellodati-blue;
  height: 50px;
  box-sizing: border-box;
  border-radius: 5px;
  padding:20px;
  color: white;
}

.employee-form select{
  width: 100%;
  height: 40px;
  border-radius: 5px;
}

.employee-form-add .add input{
  width: 100%;
  height: 40px;
  background: white!important;
  border: 1px solid gray;
  box-sizing: border-box;
  // margin: 1%;
  padding: 1%;
  border-radius: 5px;
}

.employee-inputs div{
  margin-left: 20px;
  display: inline-flex;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  flex-flow: column;
  margin-bottom: 20px;
  width: 45%;
  align-items: flex-start;
}

.employee-inputs label{
  margin-bottom: 20px;
}

.device-inputs button{
  margin-bottom: 20px;
  margin-left: 10px;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  border: none;
  background-color: $hellodati-blue;
}