@import '../../../assets/var';

.room-form {
  width: 100%;
  // height: 230px;
  background-color: #e5e5e5;
  border-radius: 5px;
}

.room-form .room-header {
  width: 100%;
  background-color: #00a8e0;
  height: 40px;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 20px;
  color: white;
  display: flex;
  align-items: center;
}

.room-form select {
  width: 45%;
  height: 40px;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 10px;

}

.room-form input {
  width: 90%;
  height: 40px;
  border: 1px solid gray;
  box-sizing: border-box;
  margin: 1%;
  padding: 1%;
  border-radius: 5px;
  margin: 10px;
  // background-color: green;

}

.room-edit-inputs .room-inputs-deux {
  display: flex;
  flex-direction: column;

}

.room-edit-inputs .room-inputs-deux div {

  display: flex;
  justify-content: space-between;
  margin-top: 10px;



  // flex-direction:column ;

}



.room-edit-inputs label {
  margin-bottom: 10px;
  width: 90%;
  margin-left: 2%;
}

.room-edit-inputs button {
  width: 45%;
  margin-bottom: 20px;
  height: 40px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: #111f35;
  color: white;
  margin: 10px;
  cursor: pointer;
}

.room-edit-inputs .room-inputs-deux .label_input div {

  flex-direction: column;
  width: 100%
}