.history-search{
  margin-bottom: 20px;

  input:not([type="datetime-local"]), select{
    height: 35px;
    box-sizing: border-box;
    border: 1px solid #7F8BA0;
    border-radius: 50px;
    min-width: 200px;
    padding-left: 20px;
    margin-left:10px;
    font-size: 1.1em;
  }

  input {
    background-color: white;
    color: #314669;
  }

  // select {
  //   background-color: #314669;
  //   color: white;
  //   position: relative;
  //   z-index: 50000;
    
  // }
  // select:focus{
  //   outline: none;
  // }

  select {

    /* styling */
    position: relative;
    background-color: #314669;
    border: thin solid #314669;
    border-radius: 50px;
    display: inline-block;
    font: inherit;
    padding: 0.1em; 
    color: white;
    text-align:center;
    margin-right: 5px!important;
    margin-left: 5px!important;
    z-index: 10;
  
    /* reset */
  
    margin: 0;      
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
  }

  /* arrows */

select.round {
  background-image:
    linear-gradient(45deg, transparent 50%, yellow 50%),
    linear-gradient(135deg, yellow 50%, transparent 50%),
    radial-gradient(#314669 65%, transparent 72%);
  background-position:
    calc(100% - 20px) calc(1em + 2px),
    calc(100% - 15px) calc(1em + 2px),
    calc(100% - .5em) .5em;
  background-size:
    5px 5px,
    5px 5px,
    1.5em 1.5em;
  background-repeat: no-repeat;
}
  .search{
    margin-left: -45px!important;
    // margin-left: -1px!important;
    padding-left:50px!important;
    z-index: 1;
  }

  input[type=datetime-local]{
    border:none;
    font-size: 1.2em;
    // margin-left: 10px;    
    padding-left: 30px;
  }
}