.service-card{
  width: 24%;
  height: 150px;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-flex;
  display: -ms-inline-flexbox;
  display: -webkit-inline-flex;
  align-items: flex-start;
  background-size: 100% 100%;
  flex-flow: column;
  justify-content: flex-end;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.2em;
  margin-bottom: 20px;
}

.service-card svg{
  width: 20px;
  margin-right: 10px;
}

.service-card p{
  padding:0;
  margin:0;
  color: white;
}



.service-name{
  margin: 20px;
}

@media screen and (max-width:1700px){
  .service-card{
    width: 32%;
  }
  
}
@media screen and (max-width:1200px){
  .service-card{
    width: 48%;
  }
  
}
@media screen and (max-width:1000px){
  .service-card{
    width: 80%;
  }
  
}
