@import "../../../assets/var.scss";

.dish-card {
  display: inline-flex;
  display: -ms-inline-flexbox;
  display: -webkit-inline-flex;
  -webkit-box-shadow: 0px 0px 27px -8px rgba(163, 163, 163, 1);
  -moz-box-shadow: 0px 0px 27px -8px rgba(163, 163, 163, 1);
  box-shadow: 0px 0px 27px -8px rgba(163, 163, 163, 1);
  margin-bottom: 20px;
  justify-content: space-between;
  border-radius: 5px;
  width: 31%;
  height: 200px;
  margin-right: 1.1111%;
  margin-left: 1.1111%;
  overflow: hidden;
  // min-width: 450px;
}

@media screen and (max-width:1700px){
  .dish-card{
    width: 47%;
  }
  .add-button{
    width: 47%!important;
  }
}
@media screen and (max-width:1200px){
  .dish-card{
    width: 80%;
  }
  .add-button{
    width: 80%!important;
  }
}

.dish-card .price{
  color:#d64451
}


.dish-card .description {
  width: 41%;
  display: flex;
  cursor: pointer;
  display: -ms-flexbox;
  display: -webkit-flex;
  flex-direction: column;
  -ms-flex-direction: column;
  justify-content: space-evenly;

  padding-left: 10px;
  padding-top: 10px;

  .desc{
    width: 100%;
    height: 40%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

}

.dish-card .description>* {
  width: 100%;
}

.dish-card .title {
  margin-top: 10px;
  color: $restaurant-blue;
  text-decoration: none !important;
}

.dish-card .description svg {
  height: 45px;
}

.dish-card a {
  text-decoration: none;
}

.dish-card .description svg path {
  fill: $restaurant-blue;

}

.dish-card .image {
  overflow: hidden;
  width: 59%;
  display: flex;
  border-radius: 0 5px 5px 0;
  display: -ms-flexbox;
  display: -webkit-flex;
  flex-direction: column;
  -ms-flex-direction: column;
  align-items: flex-end;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 20px;
  justify-content: space-between;
  box-sizing: border-box;
}

.dish-card p {
  margin: 0;
  padding: 0;
}